.Roulette {
  -webkit-animation: Roulette infinite 20s linear;
          animation: Roulette infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}
@-webkit-keyframes Roulette {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes Roulette {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.Home_line {
  background-image: -webkit-gradient(linear, right top, left top, from(rgba(253, 138, 117, 0)), color-stop(51%, #FD8A75), to(rgba(253, 138, 117, 0)));
  background-image: linear-gradient(-90deg, rgba(253, 138, 117, 0) 0%, #FD8A75 51%, rgba(253, 138, 117, 0) 100%);
}
.progress_ls .ant-progress-circle .ant-progress-text {
  color: #FFFFFF;
  font-size: 14px;
}
